.container {
	/* position: fixed; */
	z-index: 10;
	padding: 1rem 5rem;
	color: white;
	font-size: 1.3rem;
	display: flex;
	justify-content: space-between;
}

.navLinkSection {
	display: flex;
	justify-content: space-between;
	column-gap: 2rem;
}

.navLinks {
	color: white;
	text-decoration: none;
	transition: all 0.2s ease-in;
}

.navLinks:hover {
	color: antiquewhite;
}
